import React from "react"
import { Link, graphql } from "gatsby"
import Img from "gatsby-image"
import kebabCase from "lodash/kebabCase"
import { DiscussionEmbed } from "disqus-react"

//Components
import SEO from "../components/seo"
import Layout from "../components/layout"
import MailList from "../components/maillist"
import RelatedArticles from "../components/relatedposts"
import Like from "../components/common/like"

export default function Template({ data }) {
  //required variables
  const post = data.markdownRemark
  const category = post.frontmatter.category

  //comments system from disque
  const disqusShortname = "codeitarabic"
  const disqusConfig = {
    identifier: post.id,
    title: post.frontmatter.title,
  }

  return (
    <Layout>
      <SEO title={post.frontmatter.title} description={post.excerpt} />
      <div className="d-flex justify-content-center">
        <div className="col-md-8">
          <br />
          <br />
          <h2>
            <b dir="auto">
              <i className="far fa-bookmark"></i> {post.frontmatter.title}
              <br />
            </b>
          </h2>{" "}
          <Link to={`/category/${kebabCase(category)}`}>
            {" "}
            <span className="badge badge-info">
              {category} <i className="fas fa-clipboard-list"></i>
            </span>
          </Link>{" "}
          <span className="badge badge-info">
            {post.frontmatter.date} <i className="fas fa-clipboard-list"></i>
          </span>
          {"  "}
          <Img
            className="card-img-top"
            fluid={post.frontmatter.image.childImageSharp.fluid}
          />
          <br />
          <div
            dir="auto"
            className="unselectable"
            dangerouslySetInnerHTML={{ __html: post.html }}
          />
          <div>
            <h5>
              <Like /> <small>:هل أعجبك هذا المقال</small>{" "}
            </h5>{" "}
          </div>
          <ul>
            {post.frontmatter.tags.map(tag => (
              <Link key={tag} to={`/tags/${kebabCase(tag)}`}>
                {" "}
                <span className="badge badge-warning">{tag}</span>
              </Link>
            ))}{" "}
            : <i className="fa fa-tags fa-sm"></i> الوســوم
          </ul>
          <h6>
            تم نشرة في <i className="far fa-calendar-alt"></i> :{" "}
            {post.frontmatter.date}
          </h6>
          <h6>
            <Link to={`/category/${kebabCase(category)}`}>
              {" "}
              <span className="badge badge-info">
                {category} <i className="fas fa-clipboard-list"></i>
              </span>
            </Link>
          </h6>
          <Link to="/" className="btn btn-secondary btn-sm">
            <i className="far fa-arrow-alt-circle-left"></i> العودة للخلف
          </Link>
          <br />
          <br />
          <b>
            {" "}
            : موضوعات ذات صلة <i className="far fa-copy"></i>
          </b>
          <hr />
          <RelatedArticles
            postCat={post.frontmatter.category}
            postSlug={post.fields.slug}
          />
          <hr />
          <div id="content-mobile">
            <div className="p-3 mb-2 bg-success text-white">
              القائمة البريدية
            </div>
            <MailList />
          </div>
          <hr />
          <b>
            : أضف تعليقك علي الموضوع <i className="far fa-comment-dots"></i>
          </b>
          <DiscussionEmbed shortname={disqusShortname} config={disqusConfig} />
        </div>
      </div>
    </Layout>
  )
}

export const postQuery = graphql`
  query PostBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      id
      fields {
        slug
      }
      excerpt
      frontmatter {
        title
        category
        author
        date
        tags
        image {
          childImageSharp {
            fluid(maxWidth: 750, maxHeight: 300) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`
