import React, { Component } from "react"

class Like extends Component {
  render() {
    //event handlers
    function handleLike() {
      let element = document.getElementById("like-botton")
      if (element.className === "fas fa-heart") {
        element.setAttribute("class", "far fa-heart")
        element.innerHTML = ""
      } else if (element.className === "far fa-heart") {
        element.setAttribute("class", "fas fa-heart")
        element.innerHTML = " معجب به"
      }
    }

    return (
      <i
        id="like-botton"
        onClick={handleLike}
        style={{ cursor: "pointer", color: "#FF0000" }}
        className="far fa-heart"
        aria-hidden="true"
      ></i>
    )
  }
}

export default Like
